@import url(https://fonts.googleapis.com/css?family=Russo+One&display=swap&subset=cyrillic,latin-ext);
@import url(https://fonts.googleapis.com/css?family=Odibee+Sans&display=swap);
*{margin:0;padding:0;font-family:'Russo One', sans-serif;color:rgba(255,255,255,0.911)}body{background-position:0 0;background-position:initial;background-repeat:repeat;background-size:auto auto;background-size:initial;min-height:100vh;position:relative;background:url(../../static/media/background.8bc907b7.jpg)}.app{width:70vw;max-width:800px;min-width:300px;height:80vh;max-height:700px;padding:15px;position:absolute;top:50%;left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%);text-align:center;background:rgba(0,0,0,0.26);border-radius:20px}.searchBox{display:flex}.searchBox form{width:-webkit-fill-available}.searchBox button{font-size:20px;padding:10px 20px;border-radius:10px;background:rgba(255,0,0,0.397);border:3px solid rgba(0,0,0,0.26);cursor:pointer}.searchBox .searchBar{background:rgba(255,255,255,0.212);border-radius:10px;border:3px solid rgba(0,0,0,0.26);color:#fff;font-size:20px;padding:10px 20px;-webkit-appearance:none;-moz-appearance:none;appearance:none;outline:none;text-align:center}.searchBox .searchBar::-webkit-input-placeholder{color:#fff}.searchBox .searchBar:-ms-input-placeholder{color:#fff}.searchBox .searchBar::-ms-input-placeholder{color:#fff}.searchBox .searchBar::placeholder{color:#fff}.searchBox .searchBar:hover{background:rgba(255,255,255,0.76)}.searchBox .searchBar:focus{background:#fff;color:black}.mainInfo{padding:15px;margin:25px 0 0 0;border:1px solid white;font-size:20px}.mainInfo .temp{display:flex;justify-content:center;margin-top:50px}.mainInfo .temp i{color:#474747;font-size:150px;margin:0 0 0 25px}.mainInfo .temp h1{font-size:200px;font-family:'Odibee Sans', cursive;color:#5a5a5a}.mainInfo .additional{display:flex;justify-content:center}.mainInfo .additional h3{margin:0 30px}

